.navMenuToggle{

  height: 80px;
  z-index: 101;
  position: absolute;

  nav {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      width: 300px;
    }
    
    .background {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      width: 300px;
      background: transparent;
    }
    
    button {
      outline: none;
      border: none;
      -webkit-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      cursor: pointer;
      position: absolute;
      top: 18px;
      left: 15px;
      width: 50px;
      height: 50px;
      border-radius: 50%;
      background: transparent;
    }
    
    ul,
    li {
      margin: 0;
      padding: 0;
    }
    
    ul {
      padding: 25px;
      position: absolute;
      top: 100px;
      width: 230px;
    }
    
    li {
      list-style: none;
      margin-bottom: 20px;
      display: flex;
      align-items: center;
      cursor: pointer;
    }
    
    .icon-placeholder {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      flex: 40px 0;
      margin-right: 20px;
    }
    
    .text-placeholder {
      border-radius: 5px;
      width: 200px;
      height: 30px;
      flex: 1;
      display: flex;
      justify-content: center;
    }

    .text-inPlaceholder {
      color: white;
      font-family: font-jedi;
    }
    
    .refresh {
      padding: 10px;
      position: absolute;
      background: rgba(0, 0, 0, 0.4);
      border-radius: 10px;
      width: 20px;
      height: 20px;
      top: 10px;
      right: 10px;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
    }

    @media only screen and (max-width: 1440px) {

      height: 100%;

      .background {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        width: 250px;
        height: 1200px;
        background: black;
      }
    
    }
}